import React, { Component } from 'react';
import {GQL} from '../Api';
import { Link } from "react-router-dom";


//const ApiExperiments  =new  Api("experiments")

const ApiExperiments=new GQL(`
{
  allExperimentDetails(condition: {isObservate: true}) {
    edges {
      node {
        id
        name
        price
        observate
        experimentByExperimentId {
          id
          proyectByProyectId {
            id
            name
            userByOwnerId {
              id
              name
              lastName
            }
          }
          userByOwnerId {
            id
            name
            lastName
          }
        }
      }
    }
  }
}

`)


function parseDate(jsonDateString : string)  {
  var d= new Date(jsonDateString);
  return d.getDate() + "/" + d.getMonth() + "/" + d.getFullYear(); 
}


class Resumen extends Component {
  state={experiments:[]}

  constructor(props:any) {
    super(props)

    ApiExperiments.getAll().then((experiments :any) => {  

      console.log(experiments)
     this.setState({experiments:experiments.data.allExperimentDetails.edges}) 
}  )
    

}

  render() {
    return (  <div className="card">

    
    <div className="table-responsive">
      <table className="table table-hover no-wrap">
        <thead>
          <tr>
            <th className="text-center">#</th>
            <th>Experimento</th>
            <th>Creador</th>
            <th>Proyecto</th>
            <th>Responsable</th>

            <th>Observacion</th>
            <th>Costo</th>
            <th>Muestra</th>
          </tr>
        </thead>
        <tbody>
           {this.state.experiments.map((experiment:any,ix) => {
          return     <tr key={ix}>
         
            <td className="text-center">{experiment.node.experimentByExperimentId.id}</td>
            <td className="txt-oflo">{experiment.node.name}</td>
            <td className="txt-oflo">{experiment.node.experimentByExperimentId.userByOwnerId.name} {experiment.node.experimentByExperimentId.userByOwnerId.lastName} </td>
            <td className="txt-oflo">{experiment.node.experimentByExperimentId.proyectByProyectId.name}  </td>      
            <td className="txt-oflo">{experiment.node.experimentByExperimentId.proyectByProyectId.userByOwnerId.name} {experiment.node.experimentByExperimentId.proyectByProyectId.userByOwnerId.lastName} </td>
            <td className="txt-oflo">{experiment.node.observate}</td>

            <td className="txt-oflo">{experiment.node.price/100} U.F.</td>

            <td className="txt-oflo"> <Link to={"/proyects/"+experiment.node.id} > Solicitud </Link> <Link to={"/proyects/"+experiment.node.id} >  Descargar </Link></td>
       
          </tr>     
          })} 
          
        </tbody>
      </table>
    </div>
  </div>
)

    }
}
export default Resumen;
