
import React, { Component } from 'react';
import { GQL } from '../../Api';

class Transfer extends Component<{}, {proyects:any}> {
  user: any


  constructor(props: any) {
    super(props)

    this.state = {  proyects: [] }
    //   this.state={machines:[] as Machines ,owned:null,proyects:null}
    this.user = new GQL(`{

      allProyects {
        edges {
          node {
            id
            name
            
          }
        }
      }
    }
    `).getAll().then((out:any) => { this.setState({proyects:out.data.allProyects.edges }) } )



  }

  render() {
    return <div>
      <div className="row page-titles">
        <div className="col-md-6 col-8 align-self-center">
          <h3 className="text-themecolor mb-0 mt-0">Transferir saldo desde mi cuenta  a un proyecto </h3>
          <ol className="breadcrumb">
            <li className="breadcrumb-item"><a href="javascript:void(0)">Inicio</a></li>
            <li className="breadcrumb-item active">Transferencia de saldos </li>
          </ol>
        </div>

      </div>





      <div className="row">
        <div className="col-lg-6">
          <div className="card">
            <div className="card-body">
              <h4 className="card-title">Transferir saldos en U.F.</h4>
              <form className="form pt-3">
                <div className="form-group">
                  <label htmlFor="exampleInputuname">Usuario </label>
                  <div className="input-group">
                    <div className="input-group-prepend">
                      <span className="input-group-text" id="basic-addon1">
                        <i className="ti-user" />
                      </span>
                    </div>
                    <select className="form-control" >
                            <option>Elegir un proyecto</option>

                            {this.state.proyects.map((proyect:any,ix:number)=><option key={ix} value={proyect.node.id }> {proyect.node.name} </option>  )}

                    </select>                   </div>
                </div>
                <div className="form-group">
                  <label htmlFor="exampleInputEmail1">Saldo</label>
                  <div className="input-group">
                    <div className="input-group-prepend">
                      <span className="input-group-text" id="basic-addon1">
                        <i className="ti-money" />
                      </span>
                    </div>
                    <input type="text" className="form-control" id="exampleInputEmail1" placeholder="Agregar saldo en U.F." />
                  </div>
                </div>

                <button type="submit" className="btn btn-success waves-effect waves-light mr-2">Agregar saldo</button>
                <button type="submit" className="btn btn-inverse waves-effect waves-light">Cancel</button>
              </form>
            </div>
          </div>
        </div>
        <div className="col-lg-6">
          <div className="card">
            <div className="card-body">
              <h4 className="card-title">Transferir creditos </h4>
              <form className="form pt-3">
                <div className="form-group">
                  <label htmlFor="exampleInputuname">Proyecto </label>
                  <div className="input-group">
                    <div className="input-group-prepend">
                      <span className="input-group-text" id="basic-addon1">
                        <i className="ti-user" />
                      </span>
                    </div>
                    <select className="form-control" >
                            <option>Elegir un proyecto</option>

                            {this.state.proyects.map((proyect:any,ix:number)=><option key={ix} value={proyect.node.id }> {proyect.node.name} </option>  )}

                    </select>                    </div>
                </div>
                <div className="form-group">
                  <label htmlFor="exampleInputEmail1">Saldo</label>
                  <div className="input-group">
                    <div className="input-group-prepend">
                      <span className="input-group-text" id="basic-addon1">
                        <i className="ti-money" />
                      </span>
                    </div>
                    <input type="text" className="form-control" id="exampleInputEmail1" placeholder="Agregar saldo en U.F." />
                  </div>
                </div>

                <button type="submit" className="btn btn-success waves-effect waves-light mr-2">Agregar saldo</button>
                <button type="submit" className="btn btn-inverse waves-effect waves-light">Cancel</button>
              </form>
            </div>
          </div>
        </div>

      </div>



    </div>


  }

}

export default Transfer;