import React, { Component } from 'react';
import Resumen from './resumen';
import Aprobar from './aprobar';
import { GQL } from '../Api';
import { Link } from 'react-router-dom';



const experiments = new GQL(`{
  allProyects(condition: {ownerId: 2}) {
    edges {
      node {
        experimentsByProyectId {
          edges {
            node {
              experimentDetailsByExperimentId {
                totalCount
              }
            }
          }
        }
      }
    }
  }
  allProyectsUsers(condition: {userId: 2} )  {
    edges {
      node {
        proyectByProyectId {
          experimentsByProyectId {
            edges {
              node {
                experimentDetailsByExperimentId {
                  totalCount
                }
              }
            }
          }
        }
      }
    }
  }
}`)



const encurso = new GQL(`{
  allProyects(condition: {ownerId: 1}) {
    edges {
      node {
        experimentsByProyectId {
          edges {
            node {
              experimentDetailsByExperimentId(condition:{isReady:false}) {
                totalCount
              }
            }
          }
        }
      }
    }
  }
  allProyectsUsers(condition: {userId: 2} )  {
    edges {
      node {
        proyectByProyectId {
          experimentsByProyectId {
            edges {
              node {
                experimentDetailsByExperimentId(condition:{isReady:false}) {
                  totalCount
                }
              }
            }
          }
        }
      }
    }
  }
}`)

const dependientes = new GQL(`{
  allProyects(condition:{ownerId:2}) {
    edges {
      node {
        proyectsUsersByProyectId {
          totalCount
        
        }
      }
    }
  }
}`)

const montos = new GQL(`{
  allProyects(condition:{ownerId:2}) {
    edges {
      node {
        proyectsUsersByProyectId {
          totalCount
        
        }
      }
    }
  }
}`)



class Dashboard extends Component {
  state = { money: 0, dependientes: 0, experimentos: 0, encurso: 0 }
  constructor(props: any) {
    super(props)

    dependientes.getAll().then((data: any) => {
      this.setState({ dependientes: data.data.allProyects.edges.reduce((a: number, b: any) => a +  b.node.proyectsUsersByProyectId.totalCount, 0) })
    })
 
 

    encurso.getAll().then((data: any) => {
      this.setState({ encurso: data.data.allProyects.edges.reduce((a: number, b: any) => a + b.node.experimentsByProyectId.edges.reduce((c: number, d: any) => c + d.node.experimentDetailsByExperimentId.totalCount,0 ) , 0) })
    })
    
    experiments.getAll().then((data: any) => {
      this.setState({ experimentos: data.data.allProyects.edges.reduce((a: number, b: any) => a + b.node.experimentsByProyectId.edges.reduce((c: number, d: any) => c + d.node.experimentDetailsByExperimentId.totalCount,0 ) , 0) })
    })
 

  }

  render() {
    return <div>
      <div className="row page-titles">
        <div className="col-md-6 col-8 align-self-center">
          <h3 className="text-themecolor mb-0 mt-0">Resumen</h3>
          <ol className="breadcrumb">
            <li className="breadcrumb-item"><a href="javascript:void(0)">Inicio</a></li>
            <li className="breadcrumb-item active">Resumen</li>
          </ol>
        </div>
        <div className="col-md-6 col-4 align-self-center">

          <Link to={"/app/NewExperiment"} className="btn  hidden-sm-down btn-success"><i className="mdi mdi-plus-circle" /> Solicitar un nuevo experimento </Link>
 

          &nbsp;&nbsp;
          <a href="http://uci-fq.uc.cl:81/GoToOldSystem/"className="btn float-right hidden-sm-down btn-info"><i className="mdi mdi-plus-circle" /> Ir al portal antiguo </a>


        </div>
      </div>
      {/* ============================================================== */}
      {/* End Bread crumb and right sidebar toggle */}
      {/* ============================================================== */}
      {/* ============================================================== */}
      {/* Start Page Content */}
      {/* ============================================================== */}
      <div className="row">
        {/* Column */}
        <div className="col-md-6 col-lg-2 col-xlg-2">
          <div className="card card-inverse card-info">
            <div className="box bg-info text-center">
              <h1 className="font-light text-white">10 UF</h1>
              <h6 className="text-white">Monto Disponible</h6>
            </div>
          </div>
        </div>
        <div className="col-md-6 col-lg-2 col-xlg-2">
          <div className="card card-inverse card-info">
            <div className="box bg-info text-center">
              <h1 className="font-light text-white">10 CR</h1>
              <h6 className="text-white">Creditos Disponible</h6>
            </div>
          </div>
        </div>

        {/* Column */}
        <div className="col-md-6 col-lg-3 col-xlg-3">
          <div className="card card-primary card-inverse">
            <div className="box text-center">
              <h1 className="font-light text-white">{this.state.dependientes}</h1>
              <h6 className="text-white">Dependientes</h6>
            </div>
          </div>
        </div>
        {/* Column */}
        <div className="col-md-6 col-lg-3 col-xlg-3">
          <div className="card card-inverse card-success">
            <div className="box text-center">
              <h1 className="font-light text-white">{this.state.experimentos}</h1>
              <h6 className="text-white">Experimentos</h6>
            </div>
          </div>
        </div>
        {/* Column */}
        <div className="col-md-6 col-lg-2 col-xlg-2">
          <div className="card card-inverse card-warning">
            <div className="box text-center">
              <h1 className="font-light text-white">{this.state.encurso}</h1>
              <h6 className="text-white">Experimentos en curso</h6>
            </div>
          </div>
        </div>
      </div>
      {/* Row */}
      <div className="row">
        {/* Column */}

        <Aprobar />
      </div>

      <div className="col-lg-12">

        <Resumen />

      </div>

      {/* Row */}
      {/* Row */}
      <div className="row">

        {/* Column */}

        {/* Column */}
      </div>
      {/* Row */}

      {/* Row */}
      {/* Row */}


    </div>


  }

}

export default Dashboard;
