import React, { Component } from 'react';
import {GQL} from '../Api';
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'
import Experiment from './modal';

//const ApiExperiments  =new  Api("experiments")

const ApiExperiments=new GQL(`{
  allExperiments(orderBy:ID_DESC,first:200) {
    edges {
      node {
        id
        groupPay

        createdAt
        experimentDetailsByExperimentId {
          edges {
            node {
              id
              name
              file
              isReady
              isCanceled
              isObservate
              price
              request
            }
          }
        }
        machineByMachineId {
          name
        }
        userByOwnerId {
          id
          name
          lastName
          email
        }
        proyectByProyectId  {
          id
          name
        }
        
      }
    }
  }
}`)


function parseDate(jsonDateString : string)  {
  var d= new Date(jsonDateString);
  return d.getDate() + "/" + d.getMonth() + "/" + d.getFullYear(); 
}


class Resumen extends Component<{},{modal:boolean,experiments:any[],experiment:any} > {
  state={experiments:[],modal:false,experiment:null}

  constructor(props:any) {
    super(props)

    ApiExperiments.getAll().then((experiments :any) => {  

      console.log(experiments)
     this.setState({experiments:experiments.data.allExperiments.edges}) 
}  )
    

}

  render() {
    return (  <div className="card">




     {this.state.modal?<Experiment  experiment={this.state.experiment} close={()=>{ this.setState({modal:false})} }/>:null}

    <div className="card-body">
      <div className="d-flex no-block">
        <h4 className="card-title">Resumen Mensual</h4>
        
      </div>
      <h6 className="card-subtitle">Ultimos 30 dias </h6>
    </div>
    <div className="card-body bg-light">
      <div className="row">
        <div className="col-6">
          <h2 className="mb-0">Resumen por mes</h2>
          <select className="custom-select">
            <option selected>Ultimos 30 dias</option>
            <option value={1}>Septiembbre 2018</option>
            <option value={2}>Agosto 2019</option>
            <option value={3}>Julio 2019</option>
          </select>
        
          </div>
        <div className="col-6 align-self-center display-6 text-info text-right"> {this.state.experiments.reduce( (a:number, b:any) => a + b.node.groupPay==2?b.node.experimentDetailsByExperimentId.edges.reduce( (a:number, b:any) => a + b.node.price, 0):0  , 0)} :CR  ${this.state.experiments.reduce( (a:number, b:any) => a + b.node.groupPay==1?b.node.experimentDetailsByExperimentId.edges.reduce( (a:number, b:any) => a + b.node.price, 0):0  , 0)} : U.F. </div>
        
      </div>
    </div>
    <div className="table-responsive">
      <table className="table table-hover no-wrap">
        <thead>
          <tr>
            <th className="text-center">#</th>
            <th>Equipo</th>
            <th>Usuario</th>
            <th>Proyecto</th>
            <th>Estado</th>
            <th>Fecha</th>
            <th>Precio</th>
          </tr>
        </thead>
        <tbody>
           {this.state.experiments.map((experiment:any,ix) => {
          return <tr key={ix} onClick={()=>{this.setState({modal:true,experiment:experiment}) }}>
            {/* <a  > */}
            <td className="text-center">{experiment.node.id}</td>
            <td className="txt-oflo">{experiment.node.machineByMachineId.name}</td>
            <td className="txt-oflo">{experiment.node.userByOwnerId.name} {experiment.node.userByOwnerId.lastName}</td>
            <td className="txt-oflo">{experiment.node.proyectByProyectId.name}</td>
            <td>{experiment.node.experimentDetailsByExperimentId.edges.reduce( (a:number, b:any) => a + b.node.isReady, 0) == experiment.node.experimentDetailsByExperimentId.edges.length ? <span className="label label-success label-rouded">Finalizado</span>:null} </td>
            <td className="txt-oflo">{parseDate(experiment.node.createdAt)}</td>
      
            <td><span className="text-success">
           {experiment.node.groupPay==2?<div>{experiment.node.experimentDetailsByExperimentId.edges.reduce( (a:number, b:any) => a + b.node.price, 0)} CR

           </div>:<div>
           ${experiment.node.experimentDetailsByExperimentId.edges.reduce( (a:number, b:any) => a + b.node.price, 0)} U.F.
           </div>}    
            
            
            </span></td>
            {/* </a> */}
          </tr>
       
          })} 
          
        </tbody>
      </table>
    </div>
  </div>
)

    }
}
export default Resumen;
