import { Link } from "react-router-dom";

import React, { Component } from 'react';

import { GQL } from "../Api";
import Files from "./files";


class Upload extends Component<{match:any}> {
  experiments : any
  state={load:false,data:[],experiments:null}
  constructor(props :any){
      super(props)
      this.experiments= new GQL(`{
        allExperiments(first:100,orderBy:ID_DESC,condition:{machineId:1}) {
          edges {
            node {
              id
              experimentDetailsByExperimentId(condition:{isReady:true}) {
                edges {
                  node {
                    id
                    name
                    file
                  }
                }
              }
              userByOwnerId {
                id
                name
                lastName
              }
              proyectByProyectId {
                id
                name
                userByOwnerId {
                  id
                  name
                  lastName
                }
              }
            }
          }
        }
      }    
      `)
      this.experiments.getAll().then((user:any) => {  this.setState({load:true,data:user.data.allExperiments.edges })})



    }

  render() {
    return <div>
      <div className="row page-titles">
        <div className="col-md-6 col-8 align-self-center">
          <h3 className="text-themecolor mb-0 mt-0">Cargar resultados de RMN400</h3>
          <ol className="breadcrumb">
            <li className="breadcrumb-item"><a href="javascript:void(0)">Inicio</a></li>
            <li className="breadcrumb-item active">carga de resultado</li>
          </ol>
        </div>

      </div>

      <div className="col-lg-12">


      <div className="card card-outline-info">
      
            <div className="card-body">
                <form action="#" className="form-horizontal">
                    <div className="form-body">
                        
                        
                       {this.state.experiments?<Files experiments={this.state.experiments}/>:null}

                        <h3 className="box-title">Experimentos</h3>
                        <hr className="mt-0 mb-5" />
                        {/*/row*/}
                        <div className="row">
                            <div className="col-lg-12 col-md-12">

                                <div className="table-responsive">
                                    <table className="table no-wrap">
                                        <thead>
                                            <tr>
                                                <th>#</th>
                                                <th>Usuario </th>
                                                <th>Proyecto</th>
                                                <th>Responsable </th>
                                                <th>Opciones </th>
                                              
                                            
                                            </tr>
                                        </thead>
                                        <tbody>
                                          {this.state.data.map((data:any,ix:number)=>{
                                                return   <tr key={ix}>
                                                <td> {data.node.id }</td>
                                                <td> {data.node.userByOwnerId.name } {data.node.userByOwnerId.lastName } </td>
                                                <td> {data.node.proyectByProyectId.name }</td>
                                                <td> {data.node.proyectByProyectId.userByOwnerId.name }  {data.node.proyectByProyectId.userByOwnerId.lastName }</td>
                                                <td><span onClick={()=>{ this.setState({experiments:data.node})} } className="label label-success">Seleccionar</span> <span className="label label-info">Enviar RMN200</span> </td>
                                            </tr> })}
 
                                            
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                        {/*/row*/}
                    </div>

                    <hr />
                    <div className="form-actions">
                        <div className="row">
                            <div className="col-md-6">
                                <div className="row">
                                    <div className="col-md-offset-3 col-md-9">
                                        <button type="submit" className="btn btn-success">Agregar nuevo proyecto</button>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-6"> </div>
                        </div>
                    </div>



                </form>
            </div>
        </div>



      </div>



    </div>


  }

}

export default Upload;