import React, { Component } from 'react';
import {GQL} from '../Api';
import { Link } from "react-router-dom";


//const ApiExperiments  =new  Api("experiments")

const ApiExperiments=new GQL(`{
  allUsers {
    edges {
      node {
        id
        name
        lastName
        email
        rut
        dv
        rolesUsersByUserId {
          edges {
            node {
              roleByRoleId {
                name
              }
            }
          }
        }
      }
    }
  }
}
`)


function parseDate(jsonDateString : string)  {
  var d= new Date(jsonDateString);
  return d.getDate() + "/" + d.getMonth() + "/" + d.getFullYear(); 
}


class Resumen extends Component {
  state={experiments:[]}

  constructor(props:any) {
    super(props)

    ApiExperiments.getAll().then((experiments :any) => {  

      console.log(experiments)
     this.setState({experiments:experiments.data.allUsers.edges}) 
}  )
    

}

  render() {
    return (  <div className="card">

    
    <div className="table-responsive">
      <table className="table table-hover no-wrap">
        <thead>
          <tr>
            <th className="text-center">#</th>
            <th>Nombre</th>
            <th>Apellido</th>
            <th>Email</th>
            <th>Rol</th>
            <th>Rut</th>
            <th>Editar</th>
          </tr>
        </thead>
        <tbody>
           {this.state.experiments.map((experiment:any,ix) => {
          return     <tr key={ix}>
         
            <td className="text-center">{experiment.node.id}</td>
            <td className="txt-oflo">{experiment.node.name}</td>
            <td className="txt-oflo">{experiment.node.lastName}</td>
            <td className="txt-oflo">{experiment.node.email}</td>
           <td className="txt-oflo">{experiment.node.rolesUsersByUserId.edges.map((role :any ,ix:number )=>{return  <div key={ix}> {role.node.roleByRoleId.name} </div> }) }</td>
            <td className="txt-oflo">{experiment.node.rut}-{experiment.node.dv}</td>

            <td className="txt-oflo"> <Link to={"/app/users/"+experiment.node.id} >  Editar </Link></td>
       
          </tr>     
          })} 
          
        </tbody>
      </table>
    </div>
  </div>
)

    }
}
export default Resumen;
