import React, { Component } from 'react';
import Resumen from './resumen';
import Api, { GQL } from '../Api';
import { Modal, Button } from 'antd';
import { InputNumber } from 'antd';

const { confirm } = Modal;



const Approves = new GQL(`{
    allProyects(condition: {ownerId: 3}) {
      edges {
        node {
          id
          name
          proyectsUsersByProyectId(orderBy: ID_ASC, condition: {activateTime: null, deletedAt: null}) {
            edges {
              node {
                id
                createdAt
                userByUserId {
                  id
                  name
                  lastName
                }
              }
            }
          }
        }
      }
    }
  }
  `)


  function parseDate(jsonDateString : string)  {
    var d= new Date(jsonDateString);
    return d.getDate() + "/" + d.getMonth() + "/" + d.getFullYear(); 
  }
  
export default class Aprobar extends Component {

    state = { users: [] }

    constructor(props: any) {
        super(props)


        Approves.getAll().then((proyects) => {
            console.log(proyects.data.allProyects.edges)
            this.setState({ users: proyects.data.allProyects.edges })

        })

    }

    onChange(value: any) {
        console.log('changed', value);
    }


    showConfirm(user: any) {
        confirm({
            title: `¿Deseas aceptar a ${user.Name} ${user.LastName}? `,
            content: (
                <div>
                    <p>Agregar cupo en UF Maximo 10 UF </p>
                    <p> <InputNumber min={1} max={10} defaultValue={1} onChange={this.onChange} /> </p>
                </div>
            ),
            onOk() {
                console.log('OK');
            },
            onCancel() {
                console.log('Cancel');
            },
        });
    }

    showDeleteConfirm(user: any) {
        confirm({
            title: '¿Estas seguro que no desea aceptar a ',
            content: `${user.Name} ${user.LastName} ?`,
            okText: 'Si',
            okType: 'danger',
            cancelText: 'No',
            onOk() {
                console.log('OK');
            },
            onCancel() {
                console.log('Cancel');
            },
        });
    }


    render() {

        return <div className="col-12">
            <div className="card">
                <div className="row">
                    <div className="col-lg-4">

                        <div className="card">
                            <div className="card-body">
                                <h4 className="card-title">Usuarios pendientes</h4>
                                <h6 className="card-subtitle">{this.state.users.reduce( (a:number, b:any) => a + b.node.proyectsUsersByProyectId.edges.length, 0)} usuarios pendientes</h6>
                                <div className="steamline mt-5">

                                    {this.state.users.map((proyect: any, ix: number) => {

                                        return <div> {proyect.node.proyectsUsersByProyectId.edges.map((user: any, jx: number) => {
                                            return <div className="sl-item" key={ix + "-" + jx}>
                                                {/* <div className="sl-left"> <img className="img-circle" alt="user" src="assets/images/users/3.jpg" /> </div> */}
                                                <div className="sl-right">
                                                    <div><a href="#">{user.node.userByUserId.name} {user.node.userByUserId.lastName}</a> <span className="sl-date">{parseDate(user.node.createdAt)} </span></div>
                                                    <div className="desc">{user.Proyect}
                                                        <br /><button onClick={() => { this.showConfirm(user) }} className="btn mt-2 mr-1 btn-rounded btn-outline-success">Aceptar</button> <button onClick={() => { this.showDeleteConfirm(user) }} className="btn mt-2 btn-rounded btn-outline-danger">Rechazar</button> </div>
                                                </div>
                                            </div>
                                        }
                                        )} </div>
                                    }

                                    )}
                                </div>
                            </div>
                        </div>



                    </div>
                    <div className="col-lg-8 border-left">
                        <div className="card-body">
                            <div className="d-flex no-block align-items-center">
                                <h4 className="font-medium text-inverse">Resumen de experimentos por semana</h4>
                                <div className="ml-auto">
                                    <ul className="list-inline">
                                        <li className="pl-0">
                                            <h6 className="text-muted"><i className="fa fa-circle mr-1 text-success" />2016</h6>
                                        </li>
                                        <li>
                                            <h6 className="text-muted"><i className="fa fa-circle mr-1 text-info" />2019</h6>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                            <div className="total-revenue4" style={{ height: '350px' }} />
                        </div>
                    </div>
                </div>
            </div>
        </div>
        {/* Column */ }
    }
}
