
import React, { Component } from 'react';
import { GQL } from "../Api";
import { thisTypeAnnotation } from '@babel/types';
import RMN from './rmn';

interface Node {
  id: number;
  name: string;
}

interface Machines {
  node: Node;
}

interface AllMachines {
  edges: Machines[];
}

interface UserByOwnerId {
  id: number;
  name: string;
  lastName: string;
}

interface Node2 {
  id: number;
  name: string;
  userByOwnerId: UserByOwnerId;
}

interface Edge2 {
  node: Node2;
}

interface AllProyects {
  edges: Edge2[];
}

interface UserByOwnerId2 {
  name: string;
  lastName: string;
}

interface ProyectByProyectId {
  name: string;
  userByOwnerId: UserByOwnerId2;
}

interface Node3 {
  proyectByProyectId: ProyectByProyectId;
}

interface Edge3 {
  node: Node3;
}

interface AllProyectsUsers {
  edges: Edge3[];
}

interface Data {
  allMachines: AllMachines;
  allProyects: AllProyects;
  allProyectsUsers: AllProyectsUsers;
}

export interface RootObject {
  data: Data;
}


class NewExperiment extends Component<{}, { load: boolean, machines: Machines[], owned: Edge2[], proyects: Edge3[], proyect_id: number, machine: number }> {
  user: any


  constructor(props: any) {
    super(props)

    this.state = { load: false, machines: [], owned: [], proyects: [],proyect_id:0 , machine:0 }
    //   this.state={machines:[] as Machines ,owned:null,proyects:null}
    this.user = new GQL(`{
        allMachines(condition:{enabled:true}) {
          edges {
            node {
              id
              name
            }
          }
        }
        allProyects(condition: {ownerId: 1}) {
          edges {
            node {
              id
              name
              userByOwnerId {
                id
                name
                lastName
              }
            }
          }
        }
        allProyectsUsers(condition: {userId: 1}) {
          edges {
            node {
              proyectByProyectId {
                name
                userByOwnerId {
                  name
                  lastName
                }
              }
            }
          }
        }
      }       
      `)
    //   this.state = {machines:{} as AllMachines }
    this.user.getAll().then((out: any) => { this.setState({ machines: out.data.allMachines.edges as Machines[], owned: out.data.allProyects.edges as Edge2[], proyects: out.data.allProyectsUsers.edges as Edge3[] }) })



  }

  handleProyect(event: any) {
    this.setState({ proyect_id: event.target.value })

  }

  handleMachine(event: any) {
    this.setState({ machine: event.target.value })
  }


  handleSubmit(event: any) {
    event.preventDefault();
    this.setState({load:true})

  }



  render() {
    return <div>
      <div className="row page-titles">
        <div className="col-md-6 col-8 align-self-center">
          <h3 className="text-themecolor mb-0 mt-0">Crear un nuevo  Experimento</h3>
          <ol className="breadcrumb">
            <li className="breadcrumb-item"><a href="javascript:void(0)">Inicio</a></li>
            <li className="breadcrumb-item active">Nuevo experimento</li>
          </ol>
        </div>

      </div>

      <div className="col-lg-12">


        <div className="card card-outline-info">

          <div className="card-body">
            <form action="#" className="form-horizontal" onSubmit={(e:any) => this.handleSubmit(e)   }>
              <div className="form-body">
                <h3 className="box-title">Información Personal                                 </h3>
                <hr className="mt-0 mb-3" />
                <div className="row">
                  <div className="col-lg-12 col-md-12">
                    <div className="form-group row">
                      <label className="col-sm-3 text-right col-form-label">Maquina</label>
                      <div className="col-md-9">
                        <select className="custom-select col-12" onChange={(e: any) => { this.handleMachine(e) }}>
                          <option >Seleccionar una maquina</option>
                          {this.state.machines.map((machine: any, ix: number) => <option key={ix} value={machine.node.id} >{machine.node.name}</option>)}
                        </select>


                      </div>
                    </div>

                  </div>
                  {/*/span*/}
                  <div className="col-lg12 col-md-12">
                    <div className="form-group  row">
                      <label className="control-label text-right col-md-3">Proyecto</label>
                      <div className="col-md-9">
                        <select className="custom-select col-12" onChange={(e: any) => { this.handleProyect(e) }} >
                          <option >Seleccione un proyecto</option>
                          {this.state.owned.map((owned: any, ix: number) => <option key={"1" + ix} value={owned.node.id} >{owned.node.name} Responsable {owned.node.userByOwnerId.name} {owned.node.userByOwnerId.lastName}</option>)}


                          {this.state.proyects.map((proyect: any, ix: number) => <option key={"A" + ix} value={proyect.node.proyectByProyectId.id} >{proyect.node.proyectByProyectId.name} Responsable {proyect.node.proyectByProyectId.userByOwnerId.name} {proyect.node.proyectByProyectId.userByOwnerId.lastName} </option>)}
                        </select>
                      </div>
                    </div>
                  </div>
                  {/*/span*/}
                </div>
                {/*/row*/}

                <hr />
                <div className="form-actions">
                  <div className="row">
                    <div className="col-md-6">
                      <div className="row">
                        <div className="col-md-offset-3 col-md-9">
                          <button type="submit" className="btn btn-success">Siguiente </button>
                          <button type="button" className="btn btn-inverse">Cancel</button>
                        </div>
                      </div>
                    </div>
                    <div className="col-md-6"> </div>
                  </div>
                </div>




              </div>



            </form>
          </div>
        </div>



      </div>


      {this.state.load ? <RMN proyect_id={this.state.proyect_id} machine_id={this.state.machine} /> : null}

      {/* <RMN proyect_id={144} machine_id={1} /> */}
    </div>


  }

}

export default NewExperiment;