const confirm = Modal.confirm;


function CallProcess(process: string, data: any, method: string) {

    let args = {}
    let url = '/api/' + process
    if (method == "GET") {

        args = {}
        var esc = encodeURIComponent;
            var query = Object.keys(data)
            .map(k => esc(k) + '=' + esc(data[k]))
            .join('&');

            url = url +"?"+query
        

    }
    else if (method == "POST") {
        args = {
            method: 'POST',
            headers: { "Content-Type": "application/json; charset=utf-8" },
            body: JSON.stringify(data)
        }

    }

    else if (method == "PUT") {

        args = {
            method: 'PUT',
            headers: { "Content-Type": "application/json; charset=utf-8" },
            body: JSON.stringify(data)
        }
        url = url +"/"+data.ID

    }


    else if (method == "DELETE") {
        args = {
            method: 'DELETE',
            headers: { "Content-Type": "application/json; charset=utf-8" }
        }
        url = url +"/"+data.ID

    }


    console.log("URL",url)



    return new Promise((resolve, reject) => {




        fetch(url, args).then(function (response) {
            return response.json();
        })
            .then(function (myJson: any) {

                resolve(myJson)
            });


    })
}



class Api {

    _process: string;
    constructor(process: string) {
        this._process = process;
    }


    new(params: any) {


        return new Promise((resolve, reject) => {

            CallProcess(this._process, params, "POST").then((data: any) => {

                resolve(data.Data)

                notification.success({
                    message: 'Se ha insertado con exito ',
                    description: 'El Id de inserccion es  ' + data.Data.ID,
                });

            })
        })

    }


    get(ID : number,params = {}) {
        console.log("ID",ID)
        return new Promise((resolve, reject) => {
            CallProcess(this._process+ "/"+ID , params, "GET").then((data: any) => { resolve(data.Data) })
        })

    }

    getAll(params = {}) {
        return new Promise((resolve, reject) => {
            CallProcess(this._process, params, "GET").then((data: any) => { resolve(data) })
        })

    }
    save(process : any) {

        return new Promise((resolve, reject) => {

            CallProcess(this._process,process,"PUT").then((data:any )=>{
                notification.success({
                    message: 'Se ha actualizado con exito ',
                    description: 'El Id ' + data.Data.ID,
                });


                resolve(data.Data)
            })

        })
    }
    delete(process : any) {

        return new Promise((resolve, reject) => {


        confirm({
            title: '¿Esta seguro que desea borrar?',
            content: 'Esta seguro que desea borrar el registro id : '+process.ID,
            okText: 'Si',
            okType: 'danger',
            cancelText: 'No',
            onOk:()=> {
              

                CallProcess(this._process,process,"DELETE").then((data:any )=>{
                    resolve(data.Data)
                })
            },
            onCancel() {
                resolve(process)
            },
          });


        })


    }

}

export default Api