import { Link } from "react-router-dom";

import React, { Component } from 'react';
import Resumen from './resumen';
import EditUserId from "./edituser";
import { GQL } from "../Api";


class EditUser extends Component<{match:any}> {
  user : any
  state={load:false,user:null,roles:[]}
  constructor(props :any){
      super(props)
      this.user= new GQL(`{
        allRoles {
          edges {
            node {
              id
              name
            }
          }
        }
        allUsers(condition: {id: ${+props.match.params.id}}, first: 1) {
          edges {
            node {
              id
              name
              lastName
              email
              address
              rut
              dv
              limit
              credit
              createdAt
              updatedAt
              rolesUsersByUserId {
                edges {
                  node {
                    roleId
                  }
                }
              }
              proyectsUsersByUserId {
                edges {
                  node {
                    proyectByProyectId {
                      id
                      name
                      userByOwnerId {
                        id
                        name
                        lastName
                      }
                    }
                    userByActivateBy {
                      id
                      name
                      lastName
                      email
                    }
                    activateTime
                    desactiveBy
                    userByDesactiveBy {
                      id
                      name
                      lastName
                      email
                    }
                  }
                }
              }
              proyectsByOwnerId {
                edges {
                  node {
                    id
                    name
                  }
                }
              }
            }
          }
        }
      }      
      `)
      this.user.getAll().then((user:any) => {  this.setState({load:true,user:user.data.allUsers.edges[0].node,roles:user.data.allRoles.edges })})



    }

  render() {
    return <div>
      <div className="row page-titles">
        <div className="col-md-6 col-8 align-self-center">
          <h3 className="text-themecolor mb-0 mt-0">Usuarios</h3>
          <ol className="breadcrumb">
            <li className="breadcrumb-item"><a href="javascript:void(0)">Inicio</a></li>
            <li className="breadcrumb-item active">Editar usuarios</li>
          </ol>
        </div>

      </div>

      <div className="col-lg-12">

   {this.state.load ?<EditUserId user={this.state.user} roles={this.state.roles} />:null}


      </div>



    </div>


  }

}

export default EditUser;
