import React, { Component } from 'react';
import Modal from 'react-bootstrap/Modal'
import Button from 'react-bootstrap/Button'
import Table from 'react-bootstrap/Table'

//

function parseDate(jsonDateString: string) {
    var d = new Date(jsonDateString);
    return d.getDate() + "/" + d.getMonth() + "/" + d.getFullYear();
}


class Experiment extends Component<{ experiment: any, close: any }, {}> {

    constructor(props: any) {
        super(props)



    }
    solvente(request: any) {

        try {
            return JSON.parse(request).solvente
        } catch (error) {
            return ""

        }
    }
    step(detail: any) {
        if (!detail.isReady && !detail.isCanceled && !detail.isObservate)
            return <Button variant="warning">En curso</Button>
        if (detail.isCanceled)
            return <Button variant="danger">Anulado</Button>
        if (detail.isObservate)
            return <Button variant="info">Observado</Button>
        return <Button href={"/"} variant="success">Descargar</Button>
    }

    render() {
        let experiment = this.props.experiment
        return (<Modal size="lg" show={true} onHide={() => { this.props.close() }}>
            <Modal.Header closeButton>
                <Modal.Title><Button variant="success">Descargar</Button> {experiment.node.userByOwnerId.name} {experiment.node.userByOwnerId.lastName} {experiment.node.machineByMachineId.name} #{experiment.node.id} </Modal.Title>
            </Modal.Header>
            <Modal.Body>

                <Table striped bordered hover>
                    <thead>
                        <tr>
                            <th>#</th>
                            <th>Nombre</th>
                            <th>Solvente</th>
                            <th>Estado</th>
                            <th>Precio</th>
                        </tr>
                    </thead>
                    <tbody>
                        {experiment.node.experimentDetailsByExperimentId.edges.map((detail: any, ix: number) => {
                            return <tr>
                                <td>{ix + 1}</td>
                                <td>{detail.node.name}</td>
                                <td>{this.solvente(detail.node.request)}</td>
                                <td>{this.step(detail.node)} </td>
                                <td>{detail.node.price} CR </td>

                            </tr>
                        })
                        }

                    </tbody>
                </Table>

            </Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={() => { this.props.close() }}>
                    Cerrar
          </Button>

            </Modal.Footer>
        </Modal>)

    }
}
export default Experiment;
