import { Link } from "react-router-dom";

import React, { Component, Props } from 'react';
import Resumen from './resumen';
import { GQL } from "../Api";
import { any } from "prop-types";
import Users from "./users";


class EditUserId extends Component<{ user: any, roles: any }> {
    user: any

    constructor(props: any) {
        super(props)


        this.user = props.user
    }

    check(rol:any,user:any){

        for(let user_rol of user.rolesUsersByUserId.edges){
            if(user_rol.node.roleId == rol.node.id){
                return true
            }
        }
        return false


    }

    render() {
        return <div className="card card-outline-info">
            <div className="card-header">
                <h4 className="mb-0 text-white">{this.user.name} {this.user.lastName}</h4>
            </div>
            <div className="card-body">
                <form action="#" className="form-horizontal">
                    <div className="form-body">
                        <h3 className="box-title">Información Personal</h3>
                        <hr className="mt-0 mb-5" />
                        <div className="row">
                            <div className="col-lg-6 col-md-12">
                                <div className="form-group row">
                                    <label className="col-sm-3 text-right col-form-label">Nombre</label>
                                    <div className="col-md-9">
                                        <input type="text" className="form-control" defaultValue={this.user.name} />
                                    </div>
                                </div>
                            </div>
                            {/*/span*/}
                            <div className="col-lg-6 col-md-12">
                                <div className="form-group  row">
                                    <label className="control-label text-right col-md-3">Apellido</label>
                                    <div className="col-md-9">
                                        <input type="text" className="form-control form-control-danger" defaultValue={this.user.lastName} />
                                    </div>
                                </div>
                            </div>
                            {/*/span*/}
                        </div>
                        {/*/row*/}

                        <div className="row">
                            <div className="col-lg-6 col-md-12">
                                <div className="form-group row">
                                    <label className="col-sm-3 text-right col-form-label">Email</label>
                                    <div className="col-md-9">
                                        <input type="text" className="form-control" defaultValue={this.user.email} />
                                    </div>
                                </div>
                                <div className="form-group row">
                                    <label className="col-sm-3 text-right col-form-label">Limite</label>
                                    <div className="col-md-9">
                                        <input type="text" className="form-control" defaultValue={this.user.limit} />
                                    </div>
                                </div>
                                
                            </div>
                            {/*/span*/}
                            <div className="col-lg-6 col-md-12">
                            <div className="form-group row">
                                    <label className="col-sm-3 text-right col-form-label">Creditos</label>
                                    <div className="col-md-9">
                                        <input type="text" className="form-control" defaultValue={this.user.credit} />
                                    </div>
                                </div>
                                <div className="form-group  row">
                                    <label className="control-label text-right col-md-3">Contraseña</label>
                                    <div className="col-md-9">
                                        <input type="password" className="form-control" defaultValue="" />
                                    </div>
                                </div>
                            </div>
                            {/*/span*/}
                        </div>
                        {/*/row*/}




                        <div className="row">
                            <div className="col-lg-6 col-md-12">
                                <div className="form-group row">
                                    <label className="control-label text-right col-md-3">Dirección</label>
                                    <div className="col-md-9">
                                        <input type="text" className="form-control" defaultValue={this.user.address} />
                                    </div>
                                </div>
                            </div>
                            {/*/span*/}
                            <div className="col-lg-6 col-md-12">
                                <div className="form-group row">
                                    <label className="control-label text-right col-md-3">Categorias</label>
                                    <div className="col-md-9">
                                        <div className="mb-2">
                                            {this.props.roles.map((rol: any, ix: number) => <div className="custom-control custom-checkbox" key={ix}>
                                                <input type="checkbox" className="custom-control-input" defaultChecked={this.check(rol,this.user )} id={"customCheck"+ix} />
                                                <label className="custom-control-label" htmlFor={"customCheck"+ix} >{rol.node.name}</label>
                                            </div>)
                                            }

                                        </div>
                                    </div>
                                </div>
                            </div>
                            {/*/span*/}
                        </div>

                        <hr />
                        <div className="form-actions">
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="row">
                                        <div className="col-md-offset-3 col-md-9">
                                            <button type="submit" className="btn btn-success">Actualizar cambios </button>
                                            <button type="button" className="btn btn-inverse">Cancel</button>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-6"> </div>
                            </div>
                        </div>



                        <h3 className="box-title">Proyectos</h3>
                        <hr className="mt-0 mb-5" />
                        {/*/row*/}
                        <div className="row">
                            <div className="col-lg-12 col-md-12">

                                <div className="table-responsive">
                                    <table className="table no-wrap">
                                        <thead>
                                            <tr>
                                                <th>#</th>
                                                <th>Proyecto</th>
                                                <th>Responsable </th>
                                                <th>Activado/por </th>
                                                <th>Desactivado</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {this.user.proyectsUsersByUserId.edges.map((proyect: any, ix: number) => {
                                                return <tr key={ix}>
                                                    <td> {proyect.node.proyectByProyectId.id}</td>
                                                    <td>{proyect.node.proyectByProyectId.name}</td>
                                                    <td> {proyect.node.proyectByProyectId.userByOwnerId.name} {proyect.node.proyectByProyectId.userByOwnerId.lastName} </td>
                                                    <td>{proyect.node.proyectByProyectId.activateTime ? null : "Aun no aceptado"} {proyect.node.proyectByProyectId.userByActivateBy ? <div>{proyect.node.proyectByProyectId.userByActivateBy.name} {proyect.node.proyectByProyectId.userByActivateBy.lastName}</div> : null}</td>
                                                    <td><span className="label label-danger">Desactivar</span> </td>
                                                </tr>
                                            })}

                                            {this.user.proyectsByOwnerId.edges.map((proyect: any, ix: number) => {
                                                return <tr key={ix}>
                                                    <td> {proyect.node.id}</td>
                                                    <td>{proyect.node.name}</td>
                                                    <td> {this.user.name} {this.user.lastName} </td>
                                                    <td> </td>
                                                    <td><span className="label label-info">Propio</span> </td>
                                                </tr>
                                            })}

                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                        {/*/row*/}
                    </div>

                    <hr />
                    <div className="form-actions">
                        <div className="row">
                            <div className="col-md-6">
                                <div className="row">
                                    <div className="col-md-offset-3 col-md-9">
                                        <button type="submit" className="btn btn-success">Agregar nuevo proyecto como colaborador</button>
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-6"> </div>
                        </div>
                    </div>



                </form>
            </div>
        </div>




    }

}

export default EditUserId;
