import React, { Component } from 'react';
import Api, { GQL } from "../Api";

const send = new Api("createExperiment")


class RMN extends Component{


  constructor(props) {
    super(props)

    // this.state = { load: false, machines: [], owned: [], proyects: [] }
  this.state={machine:null,experiment : this.DefaultState() ,experiments : [] }
    this.user = new GQL(`
      {
        allProyectsCredits(condition: {id: ${this.props.proyect_id}}) {
          edges {
            node {
              id
              name
              lastName
              proyect
              available
              credit
            }
          }
        }
        allMachines(condition:{id:2}) {
          edges {
            node {
              id
              name
            }
          }
        }
      }       
      `)
    //   this.state = {machines:{} as AllMachines }
    this.user.getAll().then((out) => { this.setState({ machine: out.data.allMachines.edges, amount: +out.data.allProyectsCredits.edges[0].node.credit  }) })



  }

  Price(name){
    let bidimensional = ["cosy","tocsy","hsqc= obj.hmbc","noesy"]

    if (bidimensional.includes(name)){
        return 2
    }
    return 1 

  }

  DefaultState(){
    let obj ={}
    obj.name=""
    obj.solvente=""
    obj.preparete=1
    obj.observate=""
    obj.recovery = true
    obj.H1 =obj.C13 = obj.dept = obj.cosy = obj.tocsy = obj.hsqc= obj.hmbc = obj.noesy  =false
    obj.B11 = obj.F19 = obj.P31 = obj.Si29 = obj.Pt125 =obj.Li7 = obj.Al27  = obj.Ag109 = obj.Otro = false
    return obj

  }

  handlePreparete(num) {
    let obj = this.state.experiment
    obj.preparete = num
    this.setState({ experiment: obj })

  }

  handleRecovery(recovery) {
     let obj = this.state.experiment
      obj.recovery = recovery
    
    this.setState({ experiment: obj })
  }

  handleOption(option){
        let obj = this.state.experiment
        obj[option] = !obj[option] 
        let amount = this.state.amount
        if (  obj[option] ){
          amount = amount-this.Price(option)
        } 
        else{
          amount = amount+this.Price(option)
        }


        this.setState({ experiment: obj,amount:amount })

  }

  handleName(event){
    let obj = this.state.experiment
    obj.name = event.target.value
    this.setState({ experiment: obj })
  }

  handleObservate(event){
    let obj = this.state.experiment
    obj.observate = event.target.value
    this.setState({ experiment: obj })
  }

  handleSolvent(event) {
    let obj = this.state.experiment
    obj.solvente = event.target.value
    this.setState({value: event.target.value});
  }

  handleSubmit(event) {
    // alert('A name was submitted: ' + this.state.value);
    event.preventDefault();
    let obj = JSON.parse(JSON.stringify (this.state.experiment) )
    this.state.experiments.push(obj)
    let exp = this.DefaultState()
    this.setState({experiment:exp})
  }
  handleSend(event) {
    // alert('A name was submitted: ' + this.state.value);
    event.preventDefault();
    send.new({user_id : 1 , machine_id: +this.props.machine_id,experiments: this.state.experiments, proyect_id: +this.props.proyect_id })
    

  }


  handleLast(){
      this.state.experiments.pop()
      this.setState({})
  }

  render() {
    return <div className="col-lg-12">


        <div className="card card-outline-info">

          <div className="card-body">
            <form  className="form-horizontal" onSubmit={(e)=> this.handleSubmit(e)}>
              <div className="form-body" >
                <h3 className="box-title float-left ">{ this.state.machine!=null? this.state.machine[0].node.name :null }                            </h3>
                <h2 className="box-title  float-right">                   Creditos disponibles :    { this.state.amount!=null? this.state.amount :null }          </h2>
              
                <div className="form-horizontal">
        <div className="panel panel-default">
          <div className="panel-heading">
            <p className="mb20"></p>
            <div className="table-responsive">
              <table className="table mb30">
                <thead>
                  <tr>
                    <th> <label htmlFor="nombre_de_muestra">Nombre de la muestra</label></th>
                    <th> <label htmlFor="solvente">Solvente</label></th>
                    <th> <label htmlFor="preparacion_muestra">Preparacion de la muestra</label></th>
                    <th> <label htmlFor="Observaciones">Observaciones/Cinetica u otros </label></th>
                    <th> <label htmlFor="devolucion">Recuperación la muestra</label></th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td><input value={this.state.experiment.name} onChange={(e)=>{this.handleName(e)} }  className="form-control ng-pristine ng-valid ng-touched" id="nombre_de_muestra" name="nombre_de_muestra" ng-model="test.nombre_de_muestra" type="text"  /></td>
                    <td><select className="form-control ng-pristine ng-untouched ng-valid"  onChange={(e)=> this.handleSolvent(e)}>                        
                    <option value=""></option>
                        <option value="Cloroformo-d1">Cloroformo-d1</option>
                        <option value="DMSO-d6">DMSO-d6</option>
                        <option value="D2O">D2O</option>
                        <option value="Acetona-d6">Acetona-d6</option>
                        <option value="Acido acetico-d4">Acido acetico-d4</option>
                        <option value="Acido F3-Acetico-d1">Acido F3-Acetico-d1</option>
                        <option value="Aceto nitrilo-d3">Aceto nitrilo-d3</option>
                        <option value="Benceno-d6">Benceno-d6</option>
                        <option value="Ciclohexano-d12">Ciclohexano-d12</option>
                        <option value="Diclorometano-d2">Diclorometano-d2</option>
                        <option value="Dietileter-d10">Dietileter-d10</option>
                        <option value="Diohexano-d6">Diohexano-d6</option>
                        <option value="DCl">DCl</option>
                        <option value="D2So4">D2So4</option>
                        <option value="Etanol-d6">Etanol-d6</option>
                        <option value="Metanol-d4">Metanol-d4</option>
                        <option value="Nitrobenceno-d5">Nitrobenceno-d5</option>
                        <option value="nitrometano-d3">nitrometano-d3</option>
                        <option value="Propanol-d8">Propanol-d8</option>
                        <option value="Piridina-d5">Piridina-d5</option>
                        <option value="Tolueno-d8">Tolueno-d8</option>
                        <option value="THF-d8">THF-d8</option>
                        <option value="DMF-d7">DMF-d7</option>
                        <option value="Otro">Otro</option></select></td>
                    <td><div ><label><input type="checkbox" onClick={(e)=>{this.handlePreparete(1) }} checked={this.state.experiment.preparete==1}/> Lista en tubo</label></div>
                      <div ><label><input type="checkbox"  onClick={(e)=>{this.handlePreparete(2) }} checked={this.state.experiment.preparete==2} /> Muestra en solvente deulterado (Requiere tubo)</label></div>
                      <div ><label><input type="checkbox"  onClick={(e)=>{this.handlePreparete(3) }} checked={this.state.experiment.preparete==3} /> Preparación UCI-FQ (se añade solvente y tubo)</label></div></td>
                    <td><textarea value={this.state.experiment.solvente} onChange={(e)=>{this.handleObservate(e)} }   className="form-control ng-pristine ng-untouched ng-valid" id="Observaciones" name="Observaciones" ng-model="test.Observaciones" rows={1} value={""} /></td>
                    <td><div ><label><input type="checkbox"  onClick={(e)=>{this.handleRecovery(true) }  } checked={this.state.experiment.recovery} />SI</label></div>
                      <div ><label><input type="checkbox" onClick={(e)=>{this.handleRecovery(false) }  } checked={!this.state.experiment.recovery} />NO</label></div></td>
                  </tr>
                </tbody>
              </table>
              <table className="table mb30">
                <thead>
                  <tr>
                    <th> <label htmlFor="H1">H1</label></th>
                    <th> <label htmlFor="C13">C13</label></th>
                    <th> <label htmlFor="dept">DEPT</label></th>
                    <th> <label htmlFor="cosy">COSY</label></th>
                    <th> <label htmlFor="tocsy">TOCSY</label></th>
                    <th> <label htmlFor="HMQC">HSQC</label></th>
                    <th> <label htmlFor="HMBC">HMBC</label></th>
                    <th> <label htmlFor="NOESY">NOESY</label></th>
                    <th> <label htmlFor="OtrosNucleos">OtrosNucleos</label></th>
                  </tr>
                  <tr>
                    <td><input onClick={(e)=>this.handleOption("H1")  } id="H1" name="H1" ng-model="test.H1" type="checkbox" checked={this.state.experiment.H1} className="ng-pristine ng-untouched ng-valid" /></td>
                    <td><input onClick={(e)=>this.handleOption("C13")  } id="C13" name="C13" ng-model="test.C13" type="checkbox" checked={this.state.experiment.C13} className="ng-pristine ng-untouched ng-valid" /></td>
                    <td><input onClick={(e)=>this.handleOption("dept")  } id="dept" name="dept" ng-model="test.dept" type="checkbox" checked={this.state.experiment.dept} className="ng-pristine ng-untouched ng-valid" /></td>
                    <td><input onClick={(e)=>this.handleOption("cosy")  } id="cosy" name="cosy" ng-model="test.cosy" type="checkbox" checked={this.state.experiment.cosy} className="ng-pristine ng-untouched ng-valid" /></td>
                    <td><input onClick={(e)=>this.handleOption("tocsy")  } id="tocsy" name="tocsy" ng-model="test.tocsy" type="checkbox" checked={this.state.experiment.tocsy} className="ng-pristine ng-untouched ng-valid" /></td>
                    <td><input onClick={(e)=>this.handleOption("hsqc")  } id="HMQC" name="HMQC" ng-model="test.HMQC" type="checkbox" checked={this.state.experiment.hsqc} className="ng-pristine ng-untouched ng-valid" /></td>
                    <td><input onClick={(e)=>this.handleOption("hmqc")  } id="HMBC" name="HMBC" ng-model="test.HMBC" type="checkbox" checked={this.state.experiment.hmqc} className="ng-pristine ng-untouched ng-valid" /></td>
                    <td><input onClick={(e)=>this.handleOption("noesy")  } id="NOESY" name="NOESY" ng-model="test.NOESY" type="checkbox" checked={this.state.experiment.noesy} className="ng-pristine ng-untouched ng-valid" /></td>
                    <td>   
                        <div ><label><input checked={this.state.experiment.B11}  onClick={(e)=>this.handleOption("B11")  }  type="checkbox"  /> B11</label></div>
                      <div ><label><input checked={this.state.experiment.F19}  onClick={(e)=>this.handleOption("F19")  }  type="checkbox"  /> F19</label></div>
                      <div ><label><input checked={this.state.experiment.P31}  onClick={(e)=>this.handleOption("P31")  }  type="checkbox"  /> P31</label></div>
                      <div ><label><input checked={this.state.experiment.Si29}  onClick={(e)=>this.handleOption("Si29")  }  type="checkbox" /> Si29</label></div>
                      <div ><label><input checked={this.state.experiment.Pt125}  onClick={(e)=>this.handleOption("Pt125")  }  type="checkbox" /> Pt125</label></div>
                      <div ><label><input checked={this.state.experiment.Li7}  onClick={(e)=>this.handleOption("Li7")  }  type="checkbox" /> Li7</label></div>
                      <div ><label><input checked={this.state.experiment.Al27}  onClick={(e)=>this.handleOption("Al27")  }  type="checkbox" /> Al27</label></div>
                      <div ><label><input checked={this.state.experiment.Ag109}  onClick={(e)=>this.handleOption("Ag109")  }  type="checkbox" /> Ag109</label></div>

                      
                      <div ><label><input checked={this.state.experiment.Otro}  onClick={(e)=>this.handleOption("Otro")  }  type="checkbox" /> Otro</label></div>
                    </td>
                  </tr>
                </thead></table>
            </div>{/* panel-body */}
 
          </div>{/* panel-default */}
        </div>
      </div>

                <hr />
                <div className="form-actions">
                  <div className="row">
                    <div className="col-md-6">
                      <div className="row">
                        <div className="col-md-offset-3 col-md-9">
                          <button type="submit" className="btn btn-success">Agregar </button>
                          <button type="button" className="btn btn-inverse">Cancel</button>
                        </div>
                      </div>
                    </div>
                    <div className="col-md-6"> </div>
                  </div>
                </div>




              </div>



            </form>
          </div>
        
        
        
        </div>


        <div className="card card-outline-info">

{this.state.experiments.length?<div className="card-body">
  <form  className="form-horizontal" onSubmit={(e)=> this.handleSend(e)}>
    
  <div className="form-horizontal">
        <div className="panel panel-default">
          <div className="panel-heading">
            <div className="table-responsive">
              <table className="table mb30">
                <thead>
                  <tr><th> <label htmlFor="nombre_de_muestra">Numero </label></th>
                    <th> <label htmlFor="nombre_de_muestra">Muestra</label></th>
                    <th> <label htmlFor="nombre_de_muestra">Costo Presupuestado</label></th>
                  </tr></thead>
                <tbody>
                 {this.state.experiments.map((experiment,ix)=><tr key={ix} className="ng-scope">
                    <th className="ng-binding">{ix+1}</th>
                    <th className="ng-binding">{experiment.name}</th>
                    <th className="ng-binding">100</th>
                 </tr> )}
                </tbody>
              </table>
            </div>{/* table-responsive */}
            <div className="panel-footer">
              <button className="btn btn-success mr5" >Enviar orden</button>
              <button className="btn btn-danger" onClick={()=>{ this.handleLast()}  } >Eliminar Ultimo</button>
            </div>{/* panel-footer */}
          </div>{/* table-responsive */} </div>{/* table-responsive */}
      </div>

  </form>
</div>:null}



</div>



      </div>


 


  }

}

export default RMN;