import React, { Component } from 'react';
import {GQL} from '../Api';
import { Link } from "react-router-dom";


//const ApiExperiments  =new  Api("experiments")

const ApiExperiments=new GQL(`
{
  allProyectsCredits{
    edges {
      node {
        id
        name
        proyect
        lastName
        available
        credit
      }
    }
  }
}

`)


function parseDate(jsonDateString : string)  {
  var d= new Date(jsonDateString);
  return d.getDate() + "/" + d.getMonth() + "/" + d.getFullYear(); 
}


class Resumen extends Component {
  state={experiments:[]}

  constructor(props:any) {
    super(props)

    ApiExperiments.getAll().then((experiments :any) => {  

      console.log(experiments)
     this.setState({experiments:experiments.data.allProyectsCredits.edges}) 
}  )
    

}

  render() {
    return (  <div className="card">

    
    <div className="table-responsive">
      <table className="table table-hover no-wrap">
        <thead>
          <tr>
            <th className="text-center">#</th>
            <th>Proyecto</th>
            <th>Responsable </th>
            <th>Saldo Max Posible</th>
            <th>Credito Max Posible</th>
            <th>Editar</th>
          </tr>
        </thead>
        <tbody>
           {this.state.experiments.map((experiment:any,ix) => {
          return     <tr key={ix}>
         
            <td className="text-center">{experiment.node.id}</td>
            <td className="txt-oflo">{experiment.node.proyect}</td>
            <td className="txt-oflo">{experiment.node.name} {experiment.node.lastName} </td>
            <td className="txt-oflo">{experiment.node.available/100} U.F. </td>
            <td className="txt-oflo">{experiment.node.credit} CR </td>      
            <td className="txt-oflo"> <Link to={"/proyects/"+experiment.node.id} >  Editar </Link></td>
       
          </tr>     
          })} 
          
        </tbody>
      </table>
    </div>
  </div>
)

    }
}
export default Resumen;
